import { yupResolver } from '@hookform/resolvers/yup'
import { LoadingButton } from '@mui/lab'
import { Box, Button, FormControl, InputLabel, MenuItem, Select, Stack, Typography } from '@mui/material'
import { useEffect, useRef, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { AiOutlineCloseCircle, AiOutlineSave } from 'react-icons/ai'
import { CiEdit } from 'react-icons/ci'
import { useAppDispatch } from 'redux/hooks'
import { UIAction } from 'redux/slices/UI'
import axiosClient from 'utils/axiosClient'
import * as yup from 'yup'

type Props = {
	apiEndpoint?: string
	fieldName?: string
	label: string
	list: { value: number; text: string }[]
	value: number
	readOnly?: boolean
	render?: () => string | JSX.Element
}
interface FormInput {
	value: any
}
export default function EditSelect({ apiEndpoint, fieldName, label, value = 0, list, readOnly, render }: Props) {
	const schema = yup.object({})
	const dispatch = useAppDispatch()
	const {
		handleSubmit,
		formState: { errors },
		setValue,
		getValues,
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			value,
		},
	})
	const [loading, setLoading] = useState(false)
	const [edit, setEdit] = useState(false)
	const ref = useRef<HTMLDivElement | null>(null)
	useEffect(() => {
		if (value) {
			setValue('value', value, {
				shouldValidate: true,
			})
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [value])

	const onSubmit: SubmitHandler<FormInput> = async ({ value }) => {
		if (apiEndpoint && fieldName) {
			setLoading(true)
			try {
				const {
					data: { message = '', error },
				} = await axiosClient.put<{ error?: boolean; message?: string }>(apiEndpoint, {
					[fieldName]: value,
				})
				setEdit(false)
				dispatch(
					UIAction.addGlobalMessage({
						message,
						type: error ? 'error' : 'success',
					})
				)
			} finally {
				setLoading(false)
			}
		}
	}
	return (
		<Box component="form" noValidate autoComplete="off" onSubmit={handleSubmit(onSubmit)} my={1}>
			{edit ? (
				<Stack
					direction={{ md: 'row', xs: 'column' }}
					alignItems="center"
					sx={{
						display: edit ? 'flex' : 'none',
						maxWidth: { md: 500, xs: 300 },
					}}
					ref={ref}
				>
					<FormControl fullWidth size="small">
						<InputLabel>{label}</InputLabel>
						<Select
							label={label}
							value={getValues('value')}
							onChange={(event) => {
								setValue('value', Number(event.target.value), { shouldValidate: true })
							}}
							error={'value' in errors}
						>
							{list.map((v) => {
								return (
									<MenuItem key={v.value} value={v.value}>
										{v.text}
									</MenuItem>
								)
							})}
						</Select>
					</FormControl>
					<Stack direction="row" sx={{ my: 1 }}>
						<Button
							sx={{
								mx: 0.5,
								px: 0.5,
								minWidth: 0,
							}}
							size="small"
							color="error"
							variant="outlined"
							onClick={() => {
								setEdit(false)
								setValue('value', value, {
									shouldValidate: true,
								})
							}}
						>
							<AiOutlineCloseCircle className="text-lg" />
						</Button>
						<LoadingButton
							disabled={Object.keys(errors).length > 0}
							loading={loading}
							sx={{
								mx: 0.5,
								px: 0.5,
								minWidth: 0,
							}}
							size="small"
							variant="outlined"
							type="submit"
						>
							<AiOutlineSave className="text-lg" />
						</LoadingButton>
					</Stack>
				</Stack>
			) : (
				<Stack
					direction={{ md: 'row', xs: 'column' }}
					spacing={2}
					sx={{ display: !edit ? 'flex' : 'none' }}
					alignItems={{
						md: 'center',
						xs: 'flex-start',
					}}
				>
					<Typography fontWeight="bold" variant="body2" flex={'none'}>
						{label}
					</Typography>
					<Stack direction="row" alignItems="center">
						{render ? (
							render()
						) : (
							<Typography>{list.find((f) => f.value === getValues('value'))?.text || ''}</Typography>
						)}
						{!readOnly && (
							<Button
								sx={{
									flex: 'none',
									mx: 1,
									p: 0.2,
									minWidth: 0,
								}}
								size="small"
								variant="outlined"
								color="info"
								onClick={() => {
									setEdit(true)
								}}
							>
								<CiEdit className="text-lg" />
							</Button>
						)}
					</Stack>
				</Stack>
			)}
		</Box>
	)
}
