import { Stack, Typography } from '@mui/material'
import { FormApi } from 'API/form'
import DeleteButton from 'components/Button/Delete'
import DesignFormObject from 'components/DesignFormObject'
import EditInput from 'components/EditInput'
import EditSelect from 'components/EditSelect'
import { PERMISSION } from 'interfaces'
import { useEffect, useState } from 'react'
import { Link, useLocation, useNavigate, useParams } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getWebs } from 'redux/slices/CMS/Web'
import { getForms } from 'redux/slices/Form'
import { getGroups } from 'redux/slices/Group'
import { UIAction } from 'redux/slices/UI'

export default function Update() {
	const { id } = useParams<{ id: string }>()
	const dispatch = useAppDispatch()
	const nav = useNavigate()
	const location = useLocation()
	const tokenInfo = useAppSelector((s) => s.UI.tokenInfo)

	const status = useAppSelector((s) => s.Form.status)
	const list = useAppSelector((s) => s.Form.list)
	useEffect(() => {
		if (!id) return nav(-1)
		if (status === 'init') dispatch(getForms())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [id])

	const webs = useAppSelector((s) => s.CMS.Web.list)
	const webStatus = useAppSelector((s) => s.CMS.Web.status)
	const groups = useAppSelector((s) => s.Group.list)
	const groupStatus = useAppSelector((s) => s.Group.status)
	useEffect(() => {
		if (groupStatus === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	useEffect(() => {
		if (webStatus === 'init') dispatch(getWebs())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	const [loading, setLoading] = useState(false)

	if (!id || !tokenInfo) return null
	const form = list.find((f) => f.id === parseInt(id))
	const group = groups.find((f) => f.id === tokenInfo.groupId)
	if (!form || !group) return null
	return (
		<Stack gap={1} my={2}>
			<Stack direction="row" alignItems="center" justifyContent="space-between" gap={2} sx={{}}>
				<Typography
					color="primary.dark"
					variant="h5"
					sx={{
						fontWeight: 600,
					}}
				>
					フォーム設定
				</Typography>
				<Link to={`/form/${id}/delete`} state={{ location }}>
					<DeleteButton text="削除" sx={{ width: 150, py: '2px', fontSize: 14 }} />
				</Link>
			</Stack>
			<Stack gap={1} my={2}>
				<EditInput
					label="フォーム名"
					value={form.name}
					apiEndpoint={'/api/form/' + id}
					fieldName="name"
					readOnly={tokenInfo.permissions[PERMISSION.FORM] < 2}
					useGroupName
					group={group}
				/>
				<EditSelect
					label="使用ウェブサイト"
					list={webs.map((v) => ({ value: v.id, text: v.name }))}
					value={form.webId}
					apiEndpoint={'/api/form/' + id}
					fieldName="webId"
					readOnly={tokenInfo.permissions[PERMISSION.FORM] < 2}
				/>
				<EditInput
					label="開始日"
					datePicker
					value={form.startDate}
					apiEndpoint={'/api/form/' + id}
					fieldName="startDate"
					readOnly={tokenInfo.permissions[PERMISSION.FORM] < 2}
				/>
				<EditInput
					label="終了日"
					datePicker
					value={form.endDate}
					apiEndpoint={'/api/form/' + id}
					fieldName="endDate"
					readOnly={tokenInfo.permissions[PERMISSION.FORM] < 2}
				/>
			</Stack>
			<Stack alignItems="stretch" gap={1}>
				<DesignFormObject
					form={form}
					loading={loading}
					handleSave={async (val) => {
						try {
							setLoading(true)
							const { error, message } = await FormApi.update({
								id,
								items: val,
							})
							dispatch(
								UIAction.addGlobalMessage({
									message,
									type: error ? 'error' : 'success',
								})
							)
						} finally {
							setLoading(false)
						}
					}}
				/>
			</Stack>
		</Stack>
	)
}
