import { yupResolver } from '@hookform/resolvers/yup'
import AddIcon from '@mui/icons-material/Add'
import { LoadingButton } from '@mui/lab'
import {
	Button,
	Checkbox,
	ClickAwayListener,
	FormControlLabel,
	FormLabel,
	IconButton,
	ListItemIcon,
	ListItemText,
	MenuItem,
	MenuList,
	Paper,
	Radio,
	RadioGroup,
	Stack,
	Switch,
	TextField,
	Tooltip,
	TooltipProps,
	Typography,
	Zoom,
	styled,
	tooltipClasses,
} from '@mui/material'
import { DatePicker } from '@mui/x-date-pickers'
import { format as formatDate } from 'date-fns'
import { IFormItem } from 'interfaces'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { AiFillEdit, AiOutlineCloseCircle } from 'react-icons/ai'
import { BiHeading, BiSelectMultiple, BiText } from 'react-icons/bi'
import { BsTextareaResize } from 'react-icons/bs'
import { CiCalendarDate } from 'react-icons/ci'
import { MdAlternateEmail, MdDelete } from 'react-icons/md'
import { SiConvertio } from 'react-icons/si'
import { TbSelect } from 'react-icons/tb'
import { makeId } from 'utils/common'
import * as yup from 'yup'
import DisplayForm from './DisplayForm'

const HtmlTooltip = styled(({ className, ...props }: TooltipProps) => (
	<Tooltip {...props} classes={{ popper: className }} />
))(({ theme: _ }) => ({
	[`& .${tooltipClasses.tooltip}`]: {
		backgroundColor: 'transparent',
		color: 'rgba(0, 0, 0, 0.87)',
		maxWidth: 'none',
	},
}))

interface FormInput extends Omit<IFormItem, 'name'> {}
interface Props {
	branchItems?: IFormItem[]
	setBranchItems: (val: IFormItem[]) => void
}
export default function AddFormBranchItem({ branchItems = [], setBranchItems }: Props) {
	const schema = yup.object({
		display: yup.string().when('format', {
			is: (format) => format !== 'action',
			then: yup.string().required(),
		}),
	})
	const {
		register,
		handleSubmit,
		setValue,
		getValues,
		reset: formReset,
		watch,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			minlength: '',
			rows: '',
			options: [],
		},
	})
	useEffect(() => {
		const subscription = watch((value, { name, type }) => {
			if (!!value.format) {
				setTimeout(() => {
					document.getElementById('setting-form-child-item')?.scrollIntoView({
						block: 'center',
						behavior: 'smooth',
					})
				}, 100)
			}
		})
		return () => subscription.unsubscribe()
	}, [watch])
	const [open, setOpen] = useState(false)
	const [typingText, setTypingText] = useState<string>()
	const [selectRow, setSelectRow] = useState('')
	const reset = useCallback(() => {
		formReset()
		setTypingText(undefined)
		setSelectRow('')
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const displayForm = useMemo(
		() => (
			<DisplayForm
				form={branchItems}
				editMode
				selectItem={selectRow}
				setFormContent={(val) => {
					setBranchItems(val)
				}}
				remove={(val) => {
					setBranchItems(branchItems.filter((f) => f.name !== val))
				}}
				edit={(val) => {
					setSelectRow(val.name)
					setValue('display', val.display)
					setValue('format', val.format)
					setValue('maxlength', val.maxlength)
					setValue('minlength', val.minlength)
					setValue('multiSelect', val.multiSelect)
					setValue('options', val.options)
					setValue('placeholder', val.placeholder)
					setValue('required', val.required)
					setValue('rows', val.rows)
					setValue('textType', val.textType)
					setValue('useTime', val.useTime)
				}}
			/>
		),
		// eslint-disable-next-line react-hooks/exhaustive-deps
		[branchItems, selectRow]
	)
	const onSubmit: SubmitHandler<FormInput> = async (data) => {
		if (selectRow) {
			const tmp = Array.from(branchItems)
			const index = tmp.findIndex((f) => f.name === selectRow)
			if (index >= 0) {
				tmp.splice(index, 1, {
					...data,
					name: selectRow,
				})
				setBranchItems(tmp)
			}
		} else {
			setBranchItems([
				...branchItems,
				{
					...data,
					name: 'item' + makeId(),
				},
			])
		}
		reset()
	}
	const { format, maxlength, minlength, multiSelect, options, required, textType, rows, useTime } = getValues()
	return (
		<Stack>
			<Stack
				sx={{
					my: 2,
					width: '100%',
					display: 'flex',
					flexDirection: 'column',
					px: 1,
				}}
			>
				{displayForm}
			</Stack>
			<Stack
				width={'100%'}
				justifyContent="center"
				p={0.5}
				sx={{
					minHeight: 100,
				}}
			>
				{format && (
					<Stack
						component="form"
						noValidate
						autoComplete="off"
						onSubmit={handleSubmit(onSubmit)}
						sx={{ border: '1px solid #ccc', p: 2, bgcolor: '#eee' }}
						gap={2}
						id="setting-form-child-item"
					>
						<Stack gap={1}>
							<Typography>{format}</Typography>
							{['文章（回答なし）'].includes(format) && (
								<TextField
									variant="outlined"
									required
									margin="dense"
									autoFocus
									multiline
									minRows={3}
									fullWidth
									size="small"
									label={'仕出し'}
									{...register('display')}
									error={'display' in errors}
								/>
							)}
							{['一覧', '選択肢', '短文', 'メールアドレス', '長文', '日付'].includes(format) && (
								<>
									<TextField
										variant="outlined"
										required
										margin="dense"
										size="small"
										autoFocus
										label={'表示テキスト'}
										InputLabelProps={{
											shrink: true,
										}}
										placeholder="項目のタイトルや質問などの文字です"
										{...register('display')}
										error={'display' in errors}
									/>
									{['短文', 'メールアドレス', '長文'].includes(format) && (
										<TextField
											variant="outlined"
											margin="dense"
											size="small"
											label={'例文'}
											InputLabelProps={{
												shrink: true,
											}}
											placeholder="何も入力してない時に表示する文字です"
											{...register('placeholder')}
											error={'placeholder' in errors}
										/>
									)}
									<FormControlLabel
										control={
											<Switch
												checked={required || false}
												onChange={(e) => {
													setValue('required', e.target.checked, { shouldValidate: true })
												}}
											/>
										}
										label="必須項目"
									/>
									{format === '選択肢' && (
										<FormControlLabel
											control={
												<Switch
													checked={multiSelect || false}
													onChange={(e) => {
														setValue('multiSelect', e.target.checked, { shouldValidate: true })
													}}
												/>
											}
											label="複数選択"
										/>
									)}
									{format === '日付' && (
										<FormControlLabel
											control={
												<Switch
													checked={useTime || false}
													onChange={(e) => setValue('useTime', e.target.checked, { shouldValidate: true })}
												/>
											}
											label="時刻指定"
										/>
									)}
								</>
							)}
						</Stack>
						{['短文', '長文', '日付'].includes(format) && (
							<Stack bgcolor={'#aaa3'} p={1} gap={1}>
								<Typography>Validate</Typography>
								<Stack direction="row" alignItems="center" gap={2}>
									{['短文', '長文'].includes(format) && (
										<>
											<TextField
												variant="outlined"
												margin="dense"
												size="small"
												label={'最低長さ'}
												value={minlength || ''}
												type="number"
												onChange={(e) => {
													const value = e.target.value
													if (!value) {
														setValue('minlength', '', { shouldValidate: true })
													} else if (/^[0-9]*$/.test(value)) {
														setValue('minlength', value, { shouldValidate: true })
													}
												}}
												error={'minlength' in errors}
											/>
											<TextField
												variant="outlined"
												margin="dense"
												size="small"
												label={'最高長さ'}
												value={maxlength || ''}
												type="number"
												onChange={(e) => {
													const value = e.target.value
													if (!value) {
														setValue('maxlength', '', { shouldValidate: true })
													} else if (/^[1-9][0-9]*$/.test(value)) {
														setValue('maxlength', value, { shouldValidate: true })
													}
												}}
												error={'maxlength' in errors}
											/>
										</>
									)}
									{format === '長文' && (
										<TextField
											variant="outlined"
											margin="dense"
											size="small"
											type="number"
											label={'行数'}
											value={rows || ''}
											onChange={(e) => {
												const value = e.target.value
												if (!value) {
													setValue('rows', '', { shouldValidate: true })
												} else if (Number(value) === 1) {
													setValue('rows', '2', { shouldValidate: true })
												} else if (/^[1-9]([0-9])*$/.test(value)) {
													setValue('rows', value, { shouldValidate: true })
												}
											}}
											error={'rows' in errors}
										/>
									)}
								</Stack>
								{format === '日付' && (
									<Stack direction="row" alignItems="center" gap={2}>
										<DatePicker
											label="最低日付"
											inputFormat="yyyy年MM月dd日"
											value={minlength ? new Date(minlength) : null}
											onChange={(date) => {
												if (date) {
													const fix = formatDate(new Date(date), 'yyyy-MM-dd')
													setValue('minlength', fix, {
														shouldValidate: true,
													})
												}
											}}
											renderInput={(params) => <TextField {...params} size="small" />}
										/>

										<DatePicker
											label="最高日付"
											inputFormat="yyyy年MM月dd日"
											value={maxlength ? new Date(maxlength) : null}
											onChange={(date) => {
												if (date) {
													const fix = formatDate(new Date(date), 'yyyy-MM-dd')
													setValue('maxlength', fix, {
														shouldValidate: true,
													})
												}
											}}
											renderInput={(params) => <TextField {...params} size="small" />}
										/>
									</Stack>
								)}
								{format === '短文' && (
									<Stack direction="row" alignItems="center" gap={2}>
										<FormLabel>テキスト入力系</FormLabel>
										<RadioGroup
											row
											value={textType || ''}
											onChange={(event) => {
												const v = event.target.value
												setValue('textType', v ? (v as any) : undefined, {
													shouldValidate: true,
												})
											}}
										>
											<FormControlLabel value="" control={<Radio />} label="なし" />
											<FormControlLabel value="en" control={<Radio />} label="英語のみ" />
											<FormControlLabel value="num" control={<Radio />} label="数字のみ" />
											<FormControlLabel value="en-num" control={<Radio />} label="英語と数字" />
											<FormControlLabel value="en-num-sym" control={<Radio />} label="英語と数字と記号" />
											<FormControlLabel value="ja" control={<Radio />} label="日本語のみ（全角）" />
										</RadioGroup>
									</Stack>
								)}
							</Stack>
						)}
						{['一覧', '選択肢'].includes(format) && (
							<Stack bgcolor={'#aaa3'} p={1} gap={1}>
								<Stack direction="row" alignItems="center" gap={2}>
									<Typography>Options</Typography>
									{options.length > 0 && (
										<IconButton
											color="info"
											onClick={() => {
												setTypingText(options.map((v) => v.text).join('\n'))
												setValue('options', [], {
													shouldValidate: true,
												})
											}}
										>
											<AiFillEdit />
										</IconButton>
									)}
								</Stack>
								<Stack>
									<Stack>
										{options.map((v, i) => {
											return (
												<Stack direction="row" gap={1} alignItems="center" key={i}>
													{i + 1}.{multiSelect ? <Checkbox /> : <Radio name="radio" value={v.text} />}
													<Typography>{v.text}</Typography>
													<IconButton
														color="error"
														onClick={() => {
															const tmp = options.filter((f) => f.id !== v.id)
															setValue('options', tmp, {
																shouldValidate: true,
															})
															if (tmp.length === 0) {
																setTypingText('')
															}
														}}
													>
														<MdDelete />
													</IconButton>
												</Stack>
											)
										})}
									</Stack>
									{typingText !== undefined && (
										<Stack direction="row" gap={1} alignItems="center">
											<TextField
												variant="outlined"
												margin="dense"
												size="small"
												multiline
												fullWidth
												minRows={5}
												value={typingText}
												placeholder="一行づつで入力してください"
												onBlur={() => {
													if (typingText.trim() !== '') {
														const ops = Array.from(new Set(typingText.split('\n').filter((f) => !!f.trim()))).map(
															(text) => ({ id: makeId(16), text })
														)
														setValue('options', ops, {
															shouldValidate: true,
														})
														setTypingText(undefined)
													}
												}}
												onChange={(e) => {
													setTypingText(e.target.value)
												}}
												error={options.filter((f) => f.text === typingText).length > 1}
											/>
											<IconButton
												color="success"
												disabled={typingText.trim() === ''}
												onClick={() => {
													setValue(
														'options',
														Array.from(new Set(typingText.split('\n').filter((f) => !!f.trim()))).map((text) => ({
															id: makeId(16),
															text,
														})),
														{
															shouldValidate: true,
														}
													)
													setTypingText(undefined)
												}}
											>
												<SiConvertio />
											</IconButton>
										</Stack>
									)}
								</Stack>
							</Stack>
						)}

						<Stack direction="row" gap={2} justifyContent="flex-end">
							<Button
								variant="outlined"
								size="small"
								startIcon={<AiOutlineCloseCircle />}
								onClick={() => {
									reset()
								}}
							>
								閉じる
							</Button>
							<LoadingButton
								variant="contained"
								color="success"
								disabled={
									Object.keys(errors).length > 0 || (['選択肢', '一覧'].includes(format) && options.length === 0)
								}
								loadingPosition="start"
								size="small"
								startIcon={<AddIcon />}
								type="submit"
							>
								確定する
							</LoadingButton>
						</Stack>
					</Stack>
				)}

				{!format && (
					<ClickAwayListener onClickAway={() => setOpen(false)}>
						<Stack
							direction="row"
							alignItems="center"
							justifyContent="center"
							gap={2}
							width={'100%'}
							sx={{
								'& .MuiTooltip-tooltip': {
									mt: '5px !important',
								},
							}}
						>
							<HtmlTooltip
								TransitionComponent={Zoom}
								PopperProps={{
									disablePortal: true,
								}}
								onClose={() => setOpen(false)}
								open={open}
								disableFocusListener
								disableHoverListener
								disableTouchListener
								title={
									<Paper elevation={2}>
										<Typography fontWeight={600} p={1} bgcolor={'#eee'}>
											回答形式の選択
										</Typography>
										<MenuList>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '文章（回答なし）', {
														shouldValidate: true,
													})
												}}
											>
												<ListItemIcon>
													<BiHeading />
												</ListItemIcon>
												<ListItemText>文章（回答なし）</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', 'メールアドレス', {
														shouldValidate: true,
													})
												}}
											>
												<ListItemIcon>
													<MdAlternateEmail />
												</ListItemIcon>
												<ListItemText>メールアドレス</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '短文', {
														shouldValidate: true,
													})
												}}
											>
												<ListItemIcon>
													<BiText />
												</ListItemIcon>
												<ListItemText>短文</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '長文', {
														shouldValidate: true,
													})
												}}
											>
												<ListItemIcon>
													<BsTextareaResize />
												</ListItemIcon>
												<ListItemText>長文</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '日付', {
														shouldValidate: true,
													})
												}}
											>
												<ListItemIcon>
													<CiCalendarDate />
												</ListItemIcon>
												<ListItemText>日付</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '選択肢', {
														shouldValidate: true,
													})
													setTypingText('')
												}}
											>
												<ListItemIcon>
													<BiSelectMultiple />
												</ListItemIcon>
												<ListItemText>選択肢</ListItemText>
											</MenuItem>
											<MenuItem
												onClick={() => {
													setOpen(false)
													setValue('format', '一覧', {
														shouldValidate: true,
													})
													setTypingText('')
												}}
											>
												<ListItemIcon>
													<TbSelect />
												</ListItemIcon>
												<ListItemText>一覧</ListItemText>
											</MenuItem>
										</MenuList>
									</Paper>
								}
							>
								<Button startIcon={<AddIcon />} onClick={() => setOpen(true)} variant="contained" color="info">
									フィールド追加
								</Button>
							</HtmlTooltip>
						</Stack>
					</ClickAwayListener>
				)}
			</Stack>
		</Stack>
	)
}
