import { Box, Stack } from '@mui/material'
import { IFormItem } from 'interfaces'
import { useCallback, useEffect, useMemo } from 'react'
import { renderToStaticMarkup } from 'react-dom/server'

interface Props {
	formItems: IFormItem[]
}
function showLengthCanInput() {
	document.querySelectorAll<HTMLElement>('[data-maxlength]').forEach((wrap) => {
		const input = wrap.querySelector<HTMLInputElement>('input[type="text"],textarea')
		if (!wrap.dataset.maxlength || !input) return
		let p = wrap.querySelector<HTMLElement>('p.length')
		if (!p) {
			p = document.createElement('p')
			p.className = 'length'
			p.style.fontSize = '0.9rem'
			wrap.appendChild(p)
		}
		p.innerText = '残り文字数: ' + (Number(wrap.dataset.maxlength) - input.value.length)
		input.oninput = () => {
			if (!p) return
			const text = input.value
			p.innerText = '残り文字数: ' + (Number(wrap.dataset.maxlength) - text.length)
		}
	})
}
export default function FormView({ formItems }: Props) {
	const getInput = useCallback((item: IFormItem, branch?: boolean) => {
		const {
			format,
			display,
			maxlength,
			minlength,
			multiSelect,
			options,
			required,
			textType,
			placeholder,
			rows,
			useTime,
			name,
		} = item
		let input: JSX.Element = <></>

		const LabelItem = ({ useFor = true }: { useFor?: boolean }) =>
			useFor ? (
				<>
					<label
						htmlFor={name}
						style={{
							display: 'flex',
							alignItems: 'center',
							margin: '10px 0 4px',
						}}
					>
						{display}
						{required ? (
							<span
								style={{
									fontWeight: 400,
									color: '#EC0000',
									marginLeft: 4,
									fontSize: 14,
								}}
							>
								必須
							</span>
						) : null}
					</label>
				</>
			) : (
				<label
					style={{
						display: 'flex',
						alignItems: 'center',
						margin: '10px 0 4px',
					}}
				>
					{display}
					{required ? (
						<span
							style={{
								fontWeight: 400,
								color: '#EC0000',
								marginLeft: 4,
								fontSize: 14,
							}}
						>
							必須
						</span>
					) : null}
				</label>
			)
		switch (format) {
			case '文章（回答なし）':
				input = <LabelItem useFor={false} />
				break
			case 'メールアドレス':
				input = (
					<>
						<LabelItem />
						<input
							type="email"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								fontSize: '0.9rem',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)
				break
			case '短文':
				let pattern: string | undefined = undefined
				if (textType === 'en') pattern = `[A-z\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'num') pattern = `[0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num') pattern = `[A-z0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num-sym') pattern = `[A-Za-z0-9\\p{P}\\p{S}\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'ja')
					pattern = `[一-龯ぁ-ゔゞ゛゜ーァ-・ヽヾ゛゜ー０-９　]{${minlength || 0},${maxlength || ''}}`

				input = (
					<>
						<LabelItem />
						<input
							type="text"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								fontSize: '0.9rem',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
							pattern={pattern}
						/>
					</>
				)
				break
			case '長文':
				input = (
					<>
						<LabelItem />
						<textarea
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '24px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								resize: rows ? 'none' : 'vertical',
								fontSize: '1rem',
							}}
							name={name}
							id={name}
							rows={parseInt(`${rows}`) || 4}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)

				break
			case '日付':
				input = (
					<>
						<LabelItem useFor={false} />
						<input
							type={useTime ? 'datetime-local' : 'date'}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
							name={name}
							id={name}
							min={minlength}
							max={maxlength}
						/>
					</>
				)
				break
			case '選択肢':
				input = (
					<>
						<LabelItem useFor={false} />
						<div
							style={{
								display: 'flex',
								flexDirection: 'column',
							}}
						>
							{options.map((v) => {
								return (
									<div
										key={v.id}
										style={{
											display: 'flex',
											padding: '2px 10px',
											gap: '4px',
											margin: '2px 0',
										}}
									>
										<input
											type={multiSelect ? 'checkbox' : 'radio'}
											name={name}
											id={v.id}
											value={branch ? v.text : v.id}
										/>
										<label htmlFor={v.id} style={{ fontWeight: 400 }}>
											{v.text}
										</label>
									</div>
								)
							})}
						</div>
					</>
				)

				break
			case '一覧':
				input = (
					<>
						<LabelItem useFor={false} />
						<select
							name={name}
							id={name}
							style={{
								width: '100%',
								height: 42,
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
						>
							<option value={''}>--</option>
							{options.map((v) => {
								return (
									<option value={v.text} key={v.id}>
										{v.text}
									</option>
								)
							})}
						</select>
					</>
				)

				break

			default:
				break
		}
		return (
			<div
				style={{
					display: branch ? 'none' : 'block',
					marginBottom: '1rem',
				}}
				data-name={name}
				data-required={required ? 'true' : undefined}
				data-maxlength={maxlength}
			>
				{input}
			</div>
		)
	}, [])
	const content = useMemo(() => {
		const items: string[] = []
		const styles: string[] = []
		formItems.forEach((v) => {
			items.push(renderToStaticMarkup(<>{getInput(v)}</>))
			if (v.branch && v.branchItems?.length) {
				v.branchItems.forEach((child) => {
					child.list.forEach((item) => {
						styles.push(`
            [data-name="${v.name}"]:has(>div input#${child.option}:checked) ~ [data-name="${item.name}"] {
              display:block !important;
            }
            `)
						items.push(renderToStaticMarkup(<>{getInput(item, true)}</>))
					})
				})
			}
		})

		const form = document.createElement('div')
		form.innerHTML = `
    ${items.join('\n')}
    `
		const style = document.createElement('style')
		style.appendChild(document.createTextNode(styles.join('\n')))
		form.appendChild(style)

		return form
	}, [formItems, getInput])
	useEffect(() => {
		showLengthCanInput()
		window.addEventListener('load', showLengthCanInput)
		return () => {
			window.removeEventListener('load', showLengthCanInput)
		}
	}, [])

	return (
		<Box
			component="form"
			sx={{
				px: 2,
			}}
			onSubmit={(e) => {
				e.preventDefault()
			}}
		>
			<Stack dangerouslySetInnerHTML={{ __html: content.outerHTML }}></Stack>
			<div
				style={{
					position: 'relative',
					display: 'flex',
					flexWrap: 'wrap',
					justifyContent: 'space-around',
					alignItems: 'center',
					margin: '2rem auto',
					maxWidth: 1024,
				}}
			>
				<button
					type="reset"
					style={{
						display: 'flex',
						width: 256,
						height: 56,
						justifyContent: 'center',
						alignItems: 'center',
						background: '#475569',
						borderRadius: 4,
						border: 'none',
						fontSize: 15,
						color: '#fff',
						fontWeight: 500,
						cursor: 'pointer',
						transition: 'all 200ms',
						margin: '10px 0',
					}}
					onClick={() => {
						document.querySelectorAll<HTMLElement>('[data-maxlength]').forEach((wrap) => {
							const input = wrap.querySelector<HTMLInputElement>('input[type="text"],textarea')
							const p = wrap.querySelector<HTMLElement>('p.length')
							if (!wrap.dataset.maxlength || !input || !p) return
							p.innerText = '残り文字数: ' + Number(wrap.dataset.maxlength)
						})
					}}
				>
					リセット
				</button>
				<button
					type="submit"
					style={{
						display: 'flex',
						width: 256,
						height: 56,
						justifyContent: 'center',
						alignItems: 'center',
						background: '#1D4ED8',
						borderRadius: 4,
						border: 'none',
						fontSize: 15,
						color: '#fff',
						fontWeight: 500,
						cursor: 'pointer',
						transition: 'all 200ms',
						margin: '10px 0',
					}}
				>
					確認画面へ
				</button>
			</div>
		</Box>
	)
}
