import { Box, Button, Stack, alpha } from '@mui/material'
import { IFormItem } from 'interfaces'
import { Fragment, useCallback, useMemo } from 'react'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'
import { AiFillEdit } from 'react-icons/ai'
import { MdRemoveCircle } from 'react-icons/md'
import stringToColor from 'string-to-color'

interface Props {
	form: IFormItem[]
	editMode?: boolean
	selectItem?: string
	remove?: (name: string) => void
	edit?: (val: IFormItem) => void
	setFormContent?: (val: IFormItem[]) => void
}
const reorder = (list: IFormItem[], startIndex: number, endIndex: number) => {
	const result = Array.from(list)
	const [removed] = result.splice(startIndex, 1)
	result.splice(endIndex, 0, removed)

	return result
}
export default function DisplayForm({ selectItem, form, editMode, remove, edit, setFormContent }: Props) {
	const getInput = useCallback((item: IFormItem) => {
		const {
			format,
			display,
			maxlength,
			minlength,
			multiSelect,
			options,
			required,
			textType,
			placeholder,
			rows,
			useTime,
			name,
			branch,
		} = item
		let input: JSX.Element = <></>

		const LabelItem = ({ useFor = true }: { useFor?: boolean }) =>
			useFor ? (
				<>
					<label
						htmlFor={name}
						style={{
							display: 'flex',
							alignItems: 'center',
						}}
					>
						{display}
						{required ? (
							<span
								style={{
									fontWeight: 400,
									color: '#EC0000',
									marginLeft: 4,
									fontSize: 14,
								}}
							>
								必須
							</span>
						) : null}
					</label>
				</>
			) : (
				<label
					style={{
						display: 'flex',
						alignItems: 'center',
					}}
				>
					{display}
					{required ? (
						<span
							style={{
								fontWeight: 400,
								color: '#EC0000',
								marginLeft: 4,
								fontSize: 14,
							}}
						>
							必須
						</span>
					) : null}
				</label>
			)
		switch (format) {
			case '文章（回答なし）':
				input = <LabelItem useFor={false} />
				break
			case 'メールアドレス':
				input = (
					<>
						<LabelItem />
						<input
							type="email"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)
				break
			case '短文':
				let pattern: string | undefined = undefined
				if (textType === 'en') pattern = `[A-z\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'num') pattern = `[0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num') pattern = `[A-z0-9\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'en-num-sym') pattern = `[A-Za-z0-9\\p{P}\\p{S}\\s]{${minlength || 0},${maxlength || ''}}`
				else if (textType === 'ja')
					pattern = `[一-龯ぁ-ゔゞ゛゜ーァ-・ヽヾ゛゜ー０-９　]{${minlength || 0},${maxlength || ''}}`

				input = (
					<>
						<LabelItem />
						<input
							type="text"
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
							name={name}
							id={name}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
							pattern={pattern}
						/>
					</>
				)
				break
			case '長文':
				input = (
					<>
						<LabelItem />
						<textarea
							placeholder={placeholder}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
								resize: rows ? 'none' : 'vertical',
							}}
							name={name}
							id={name}
							rows={parseInt(`${rows}`) || 4}
							minLength={parseInt(`${minlength}`) || undefined}
							maxLength={parseInt(`${maxlength}`) || undefined}
						/>
					</>
				)

				break
			case '日付':
				input = (
					<>
						<LabelItem useFor={false} />
						<input
							type={useTime ? 'datetime-local' : 'date'}
							style={{
								width: '100%',
								lineHeight: '32px',
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
							name={name}
							id={name}
							min={minlength}
							max={maxlength}
						/>
					</>
				)
				break
			case '選択肢':
				input = (
					<>
						<LabelItem useFor={false} />
						<div
							style={{
								display: 'flex',
								flexDirection: 'column',
								padding: '10px 0',
							}}
						>
							{options.map((v) => {
								return (
									<div
										key={v.id}
										style={{
											display: 'flex',
											padding: '2px 10px',
											gap: '4px',
											margin: '2px 0',
											borderLeft: branch ? `10px solid ${alpha(stc(v.id), 0.9)}` : 'none',
										}}
									>
										<input type={multiSelect ? 'checkbox' : 'radio'} name={name} id={v.id} />
										<label htmlFor={v.id} style={{ fontWeight: 400 }}>
											{v.text}
										</label>
									</div>
								)
							})}
						</div>
					</>
				)

				break
			case '一覧':
				input = (
					<>
						<LabelItem useFor={false} />
						<select
							name={name}
							id={name}
							style={{
								width: '100%',
								height: 42,
								padding: '4px 10px',
								borderRadius: 4,
								border: '1px solid currentColor',
							}}
						>
							<option value={''}>--</option>
							{options.map((v) => {
								return (
									<option value={v.text} key={v.id}>
										{v.text}
									</option>
								)
							})}
						</select>
					</>
				)

				break

			default:
				break
		}
		return (
			<div data-name={name} data-required={required ? 'true' : undefined} data-maxlength={maxlength}>
				{input}
			</div>
		)
	}, [])

	const context = useMemo(() => {
		return form.map((item) => {
			const input = getInput(item)
			const branchElms: JSX.Element[] = []
			if (item.branch && item.branchItems && item.branchItems.length > 0) {
				item.branchItems.forEach(({ list, option }) => {
					list.forEach((v) => {
						branchElms.push(
							<div style={{ padding: '4px 10px', background: alpha(stc(option), 0.3) }}>{getInput(v)}</div>
						)
					})
				})
			}
			return (
				<Stack
					key={item.name}
					direction="row"
					alignItems="flex-start"
					gap={2}
					sx={
						editMode
							? {
									px: 2,
									py: 1,
									'&:hover': {
										bgcolor: '#eee',
									},
									cursor: 'grab',
									'label,input,textarea,select': {
										pointerEvents: 'none',
									},
									outline: selectItem === item.name ? '3px dashed #a0c' : 'none',
							  }
							: {}
					}
				>
					<Box flex={1}>
						{input}
						{branchElms.map((v, i) => {
							return <Fragment key={i}>{v}</Fragment>
						})}
					</Box>
					{editMode && (
						<Stack direction="row" gap={1} alignItems="center" height={66}>
							<Button
								color="info"
								sx={{
									p: 0.5,
									minWidth: 0,
									fontSize: 24,
									border: '1px solid currentColor',
									borderRadius: '4px',
								}}
								onClick={() => {
									edit && edit(item)
								}}
							>
								<AiFillEdit />
							</Button>
							<Button
								color="error"
								sx={{
									p: 0.5,
									minWidth: 0,
									fontSize: 24,
									border: '1px solid currentColor',
									borderRadius: '4px',
								}}
								onClick={() => {
									remove && remove(item.name)
								}}
							>
								<MdRemoveCircle />
							</Button>
						</Stack>
					)}
				</Stack>
			)
		})
	}, [form, getInput, selectItem, editMode, edit, remove])
	const handleDragEnd = ({ destination, source }) => {
		if (!destination || !setFormContent || selectItem) return

		setFormContent(reorder(form, source.index, destination.index))
	}
	return editMode ? (
		<DragDropContext onDragEnd={handleDragEnd}>
			<Droppable droppableId="droppable">
				{(provided) => (
					<Box ref={provided.innerRef} {...provided.droppableProps}>
						{context.map((item, index) => (
							<Draggable key={index} index={index} draggableId={`${index}`}>
								{(provided, snapshot) => (
									<Box
										ref={provided.innerRef}
										{...provided.draggableProps}
										{...provided.dragHandleProps}
										sx={{
											borderColor: (theme) => alpha(theme.palette.primary.light, 0.6),
											background: snapshot.isDragging ? `linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)` : undefined,
											boxShadow: snapshot.isDragging ? '0 3px 5px 2px rgba(255, 105, 135, .3)' : undefined,
											pointerEvents: selectItem ? 'none' : 'auto',
										}}
									>
										{item}
										{(provided as any).placeholder}
									</Box>
								)}
							</Draggable>
						))}
					</Box>
				)}
			</Droppable>
		</DragDropContext>
	) : (
		<Fragment>{context}</Fragment>
	)
}
function stc(input: string) {
	return stringToColor(`${input}`)
}
